.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 1rem 0;
  margin: 1rem 0;
  border: 0.1rem solid;
}
.picture {
  position: relative;
  background-size: cover;
  /* height: 60vh;
  width: 100%; */
}
.picture .piece {
  cursor: crosshair;
  background: black;
  /* filter: blur(1px); */
  border: 0px dashed;
  text-align: center;
  position: absolute;
}
.picture .piece.matched {
  /*stop animation*/
  -webkit-animation-name: none;
          animation-name: none;
  background: rgba(255, 255, 255, 0.5);
}
.picture .piece.allowed {
  background: green;
}
.picture .piece.not-allowed {
  background: red;
}
.deck {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.deck .piece {
  cursor: pointer;
  margin: 1rem;
}
.picture .piece:before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, silver), color-stop(0%, transparent));
  background-image: -webkit-linear-gradient(left, silver 50%, transparent 0%);
  background-image: -o-linear-gradient(left, silver 50%, transparent 0%);
  background-image: linear-gradient(to right, silver 50%, transparent 0%);
  background-size: 10px 2px;
  background-repeat: repeat-x;

  -webkit-animation-name: running-lines-before;

          animation-name: running-lines-before;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.picture .piece:after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, silver), color-stop(0%, transparent));
  background-image: -webkit-linear-gradient(left, silver 50%, transparent 0%);
  background-image: -o-linear-gradient(left, silver 50%, transparent 0%);
  background-image: linear-gradient(to right, silver 50%, transparent 0%);
  background-size: 10px 2px;
  background-repeat: repeat-x;

  -webkit-animation-name: running-lines-after;

          animation-name: running-lines-after;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes running-lines-before {
  0% {
    background-position: 0% bottom;
  }
  100% {
    background-position: 100% bottom;
  }
}
@keyframes running-lines-before {
  0% {
    background-position: 0% bottom;
  }
  100% {
    background-position: 100% bottom;
  }
}
@-webkit-keyframes running-lines-after {
  0% {
    background-position: 0% top;
  }
  100% {
    background-position: 100% top;
  }
}
@keyframes running-lines-after {
  0% {
    background-position: 0% top;
  }
  100% {
    background-position: 100% top;
  }
}



